import './Score.css'
import { Card, Badge } from '@material-ui/core'


const Score = (props) => {
    let content
    switch (content) {
        case props.score:
            props.score
            break;
        case props.score === 0:
            content = "HA HA LOSER"    
    
        
    }
    return(
        <Card className="score" style={{paddingBottom:12}}>
            <Badge badgeContent={props.score} color='primary'></Badge> 
            <br/>    
            <br/>            
            <Badge badgeContent={12} color='secondary'></Badge> 
        </Card>
    )
} 
export default Score